
import {mapActions, mapGetters} from 'vuex'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import InlineXSwitch from 'vux/src/components/inline-x-switch/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'

import * as ActionTypes from '../constants/ActionTypes';

export default {
name: "PickNumber",
components: {
Checker, CheckerItem, Group, Divider, XSwitch, InlineXSwitch, CheckIcon, XButton,
Flexbox, FlexboxItem, Cell, CellBox
},
data() {
return {
animalValue: [-1],
numsValue: [-1],
colorValue: [-1],
singleValue: [-1],
userDefined: [],
animalSwitch: false,
numberSwitch: false,
colorSwitch: false,
singleSwitch: false,
selectAll: false,
lock: false
}
},
mounted: function () {
this.getNumbers();
},
computed: {
...mapGetters({
Numbers: ActionTypes.GetNumbers
}),
selectValue() {
return this.Numbers.filter(x => x.Selected === true).sort(function (x, y) {
return x.Value - y.Value;
})
}
},
methods: {
...mapActions({
getNumbers: ActionTypes.GetNumbers
}),
selectAnimal(value) {
this.executeAll();
},
selectTailNumber(value) {
this.executeAll();
},
selectColor(value) {
this.executeAll();
},
selectSingle(value) {
this.executeAll();
},
getShowCount() {
return this.Numbers.filter(x => x.Selected === true).length;
},
selectNumberItem(item) {
if (this.lock)
return;
if (item.Selected) {
item.Selected = false;
if (this.userDefined.indexOf(item) > -1) {
this.userDefined = this.removeFromArray(this.userDefined, item);
}
}
else {
if (this.userDefined.length >= 9) {
this.$vux.toast.show({
text: 'So more count',
type: 'warn'
});
} else {
item.Selected = true;
this.userDefined.push(item);
}
}
},
showNumberValue(item) {
if (item.Value.toString().length > 1)
return item.Value.toString();
else
return '0' + item.Value.toString();
},
animalSwitchChange() {
if (this.lock)
return;
this.animalValue = [-1];
this.executeAll();
},
numberSwitchChange() {
if (this.lock)
return;
this.numsValue = [-1];
this.executeAll();
},
colorSwitchChange() {
if (this.lock)
return;
this.colorValue = [-1];
this.executeAll();
},
singleSwitchChange() {
if (this.lock)
return;
this.singleValue = [-1];
this.executeAll();
},
selectAllClick() {
this.lock = !this.lock;
},
executeAll() {
if (this.lock)
return;
let exclude = [];
let include = [];
if (this.singleSwitch) {
this.singleValue.forEach(x => {
exclude = exclude.concat(this.Numbers.filter(y => Number(y.IsSingle) === x));
});
} else {
this.singleValue.forEach(x => {
include = include.concat(this.Numbers.filter(y => Number(y.IsSingle) === x));
});
}
if (this.colorSwitch) {
this.colorValue.forEach(x => {
exclude = exclude.concat(this.Numbers.filter(y => y.Color.Type === x));
});
} else {
this.colorValue.forEach(x => {
include = include.concat(this.Numbers.filter(y => y.Color.Type === x));
});
}
if (this.numberSwitch) {
this.numsValue.forEach(x => {
exclude = exclude.concat(this.Numbers.filter(y => y.TailValue === x));
});
} else {
this.numsValue.forEach(x => {
include = include.concat(this.Numbers.filter(y => y.TailValue === x));
});
}
if (this.animalSwitch) {
this.animalValue.forEach(x => {
exclude = exclude.concat(this.Numbers.filter(y => y.Animal.Type === x));
});
} else {
this.animalValue.forEach(x => {
include = include.concat(this.Numbers.filter(y => y.Animal.Type === x));
});
}
this.Numbers.forEach(x => {
x.Selected = include.indexOf(x) > -1;
if (exclude.indexOf(x) > -1)
x.Selected = false;
});
if (this.userDefined.length > 0) {
this.userDefined.forEach(x => x.Selected = true);
}
},

reset() {
this.animalValue = [-1];
this.numsValue = [-1];
this.colorValue = [-1];
this.singleValue = [-1];
this.userDefined = [];
this.animalSwitch = false;
this.numberSwitch = false;
this.colorSwitch = false;
this.singleSwitch = false;
this.selectAll = false;
this.lock = false;
this.Numbers.forEach(x => x.Selected = false);
},


removeFromArray(arr, obj) {
let length = arr.length;
for (let i = 0; i < length; i++) {
if (arr[i] === obj) {
if (i === 0) {
arr.shift();
return arr;
} else if (i === length - 1) {
arr.pop();
return arr;
} else {
arr.splice(i, 1);
return arr;
}
}
}
}
},
watch: {}
}



